import { SECOND } from '@speedlo/hooks'
import React from 'react'

import { Colors } from '../styling'
import { TouchButton } from './TouchButton'

interface IProps {
  confirmNode: ReactNode | string
  initialColor?: Colors
  confirmColor?: Colors
  confirmDisableMs?: number
  resetToInitialMs?: number
  disabled?: boolean
  onConfirm(): void
}

export const ConfirmButton: React.FC<IProps> = ({
  confirmNode,
  initialColor = Colors.greyMiddle,
  confirmColor = Colors.red,
  confirmDisableMs = 300,
  resetToInitialMs = 3 * SECOND,
  disabled,
  children,
  onConfirm,
}) => {
  const [needConfirm, setNeedConfirm] = React.useState(false)
  const buttonRef = React.useRef<HTMLButtonElement | null>(null)

  const onClick = () => {
    if (needConfirm === false) {
      buttonRef.current!.disabled = true
      setNeedConfirm(true)
      setTimeout(() => {
        if (buttonRef.current) {
          buttonRef.current.disabled = false
        }
      }, confirmDisableMs)
    } else {
      onConfirm()
    }
  }

  React.useEffect(() => {
    if (needConfirm) {
      const timer = setTimeout(() => {
        setNeedConfirm(false)
      }, resetToInitialMs)
      return () => {
        clearTimeout(timer)
      }
    }
    return undefined
  }, [resetToInitialMs, needConfirm])

  return (
    <TouchButton
      ref={buttonRef}
      onClick={onClick}
      color={needConfirm ? confirmColor : initialColor}
      disabled={disabled}
    >
      {needConfirm ? confirmNode : children}
    </TouchButton>
  )
}
