import { useRouterContext } from '@speedlo/hooks'
import { useObserver } from 'mobx-react-lite'
import React, { useCallback } from 'react'

import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import { MarginBox } from '../../../atoms/MarginBox'
import { useRoot } from '../../../atoms/RootProvider'
import { useMFinancialReportCreate } from '../../../graph/generated'
import { useSnackBar } from '../../../hooks/useSnackBar'
import { i18n } from '../../../i18n'
import { Spacing } from '../../../styling'
import { ClosureCurrentButton } from '../molecules/ClosureCurrentButton'
import { ClosureHistoryButton } from '../molecules/ClosureHistoryButton'
import { ClosureSubmitButton } from '../molecules/ClosureSubmitButton'

interface IProps {
  showCurrent: boolean
}

export const ClosureStatisticsControl: React.FC<IProps> = ({ showCurrent }) => {
  const { statistics } = useRoot()
  const { enqueueSuccess } = useSnackBar()
  const { history } = useRouterContext()

  const [createClosure] = useMFinancialReportCreate()

  const onSubmit = useCallback(async () => {
    if (!statistics.closureBranch) {
      return
    }
    const data = await createClosure({
      branchId: statistics.closureBranch,
    })
    if (data) {
      const { id } = data.result
      history.push(`/overview/closure/${id}`)
      enqueueSuccess(i18n.t`Closure has been created`)
    }
  }, [])

  return useObserver(() => (
    <MarginBox margin={`${Spacing.Middle} 0 0 0`}>
      <FlexRowSingle>
        {showCurrent ? (
          <ClosureCurrentButton />
        ) : (
          <ClosureSubmitButton
            onClick={onSubmit}
            disabled={!statistics.closureBranch}
          />
        )}
        <ClosureHistoryButton />
      </FlexRowSingle>
    </MarginBox>
  ))
}
