import React from 'react'
import styled from 'styled-components/macro'

import { mediaMobile } from '../app/Responsive'

interface IProps {
  text: string
  scrollable?: boolean
}

const StyledMonospaceOutput = styled.p`
  color: white;
  font-family: Menlo, 'Lucida Console', monospace;
  white-space: pre-wrap;
  margin: 0.2rem 0.5rem 0.5rem 0;
  line-height: 1.8rem;
  ${mediaMobile`
    width: max-content;
  `};
`

export const StyledScrollableContainer = styled.div`
  padding: 1.4rem 4rem 0 0.5rem;
  display: inline-block;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  width: 100%;

  ${mediaMobile`
  padding: 0;
  `};
`

const formatOutput = (output: string) =>
  output.replace(new RegExp('\t', 'g'), '\u0009')

const renderContent = (text: string) => (
  <StyledMonospaceOutput>{formatOutput(text)}</StyledMonospaceOutput>
)

export const MonospaceOutput: React.FC<IProps> = ({
  text,
  scrollable = false,
}) =>
  scrollable ? (
    <StyledScrollableContainer>{renderContent(text)}</StyledScrollableContainer>
  ) : (
    renderContent(text)
  )
