import { Trans } from '@lingui/macro'
import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components/macro'

import { mediaDesktop, mediaMobile } from '../../../app/Responsive'
import { MonospaceOutput } from '../../../atoms/MonospaceOutput'
import { Price } from '../../../atoms/Price'
import { useQStatsOrders } from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { useStatsFilterCurrent } from '../hooks/useStatsFilter'

const StyledStatPanel = styled.div`
  display: inline-block;
  ${mediaDesktop`
  margin: 1.4rem 4rem 0 0.5rem;
  `};
  ${mediaMobile`
    margin: 0rem 1rem 0 1rem;
  `};
`

const StyledStatLabel = styled.h1`
  font-weight: normal;
  margin: 0;
  ${mediaDesktop`
  font-size: 1.2rem;
  `};
  ${mediaMobile`
    font-size: 0.9rem;
  `};
`

const StyledStatValue = styled.h2`
  font-weight: normal;
  margin: 0;
  ${mediaDesktop`
    font-size: 3.3rem;
  `};
  ${mediaMobile`
    font-size: 1.7rem;
  `};
`

export const OrdersStatisticsSummary = observer(() => {
  const filter = useStatsFilterCurrent()
  const { data, loading } = useQStatsOrders(
    { filter: filter! },
    { skip: !filter },
  )

  if (!filter) {
    return null
  }

  if (loading) {
    return <MonospaceOutput text={i18n.t`Computing`} />
  }

  const stats = data.stats.finishedOrders

  return (
    <>
      <StyledStatPanel>
        <StyledStatLabel>
          <Trans>Total turnover</Trans>
        </StyledStatLabel>
        <StyledStatValue>
          <Price
            price={stats.total.value}
            currencyCode={stats.total.currency.code}
          />
        </StyledStatValue>
      </StyledStatPanel>
      <StyledStatPanel>
        <StyledStatLabel>
          <Trans>Online payments</Trans>
        </StyledStatLabel>
        <StyledStatValue>
          <Price
            price={stats.onlinePayments.value}
            currencyCode={stats.onlinePayments.currency.code}
          />
        </StyledStatValue>
      </StyledStatPanel>
      <StyledStatPanel>
        <StyledStatLabel>
          <Trans>Orders count</Trans>
        </StyledStatLabel>
        <StyledStatValue>
          {stats.finishedCount + stats.cancelledCount}
        </StyledStatValue>
      </StyledStatPanel>
      <StyledStatPanel>
        <StyledStatLabel>
          <Trans>Total commissions</Trans>
        </StyledStatLabel>
        <StyledStatValue>
          <Price
            price={stats.fees.value}
            currencyCode={stats.fees.currency.code}
          />
        </StyledStatValue>
      </StyledStatPanel>

      <MonospaceOutput
        text={data.stats.orderStatisticsPlain}
        scrollable={true}
      />
    </>
  )
})
