import React from 'react'
import { components } from 'react-select'
import { StylesConfig } from 'react-select/lib/styles'
import styled from 'styled-components/macro'

import { Icon } from '../../../atoms/Icon'
import { SelectControl } from '../../../atoms/SelectControl'
import { FDriverFragment } from '../../../graph/generated'
import { RM } from '../../../helpers/ramda'
import { i18n } from '../../../i18n'
import { Colors, Radiuses, Spacing } from '../../../styling'

type TDriver = FDriverFragment

interface IProps {
  drivers: ReadonlyArray<TDriver>
  selectedDriver: Maybe<TDriver>
  isLoading: boolean
  onSelect(driver: TDriver): void
  onClear(): void
  onReload(): void
}

const StyledDriverName = styled.div`
  margin: 0.1rem 0 0rem 0.2rem;
  display: inline-block;
  vertical-align: top;
  font-size: 1.25rem;
`

const StyledValueContainer = styled.div`
  margin: 0 0 0 0.7rem;
  display: flex;
  pointer-events: none;
  width: 80%;
`

const textColor = { color: Colors.white }

const styles: StylesConfig = {
  control: base => ({
    ...base,
    ...textColor,
    background: Colors.green,
    height: Spacing.ButtonHeight + Spacing.DefaultSizeUnit,
    border: 'none',
    borderWidth: 0,
    borderRadius: Radiuses.Button,
    margin: 0,
  }),
  menu: base => ({
    ...base,
  }),
  valueContainer: base => ({
    ...base,
    ...textColor,
  }),
  singleValue: base => ({
    ...base,
    ...textColor,
  }),
  placeholder: base => ({
    ...base,
    ...textColor,
    fontSize: '1.2rem',
  }),
  clearIndicator: base => ({
    ...base,
    color: Colors.white,
  }),
  indicatorSeparator: base => ({
    ...base,
    display: 'none',
  }),
}

export const OrderDriverSelect: React.FC<IProps> = ({
  drivers,
  selectedDriver,
  isLoading,
  onSelect,
  onClear,
  onReload,
}) => (
  <SelectControl<TDriver>
    options={drivers as TDriver[]}
    value={selectedDriver}
    getOptionValue={RM.prop('id')}
    isLoading={isLoading}
    isSearchable={false}
    isClearable={true}
    placeholder="Deliver order"
    width={16}
    styles={styles}
    noOptionsMessage={() => i18n.t`No drivers available`}
    onChange={driver => {
      if (driver === null) {
        onClear()
      } else {
        onSelect(driver as TDriver)
      }
    }}
    onMenuOpen={onReload}
    components={{
      ValueContainer: props => (
        <StyledValueContainer>
          <Icon iconName="directions_car" fontSize={2} color={Colors.white} />
          <components.ValueContainer {...props} />
        </StyledValueContainer>
      ),
      DropdownIndicator: () => null,
    }}
    formatOptionLabel={driver => (
      <StyledDriverName>{`${driver.firstName} ${
        driver.lastName
      }`}</StyledDriverName>
    )}
    theme={theme => ({
      ...theme,
      blurInputOnSelect: false,
      colors: {
        ...theme.colors,
        primary: Colors.greyMiddle,
        primary25: Colors.greyLight,
      },
    })}
  />
)
